
import Vue from "vue"

export default Vue.extend({
  props: {
    title: {
      type: String,
      required: true,
    },
    description: {
      type: String,
      required: false,
      default: [],
    },
  },
  computed: {
    getDescriptions() {
      return typeof this.description === "string"
        ? [this.description]
        : this.description
    },
  },
})
